import React from 'react'
import styled from '@emotion/styled'
import Contact from './Contact'

function Footer() {

    const Footer = styled.div`
        background: #262A35;
        padding: 20px 0 0 0;
        h2{
            color:white;
        }
        .copyright{
            color:white;
            padding:12px;
            text-align:center;
            background: #1B242F;
            margin-top:32px;
        }
    `

    return (
        <Footer>
            <h2>CONTACT</h2>
            <Contact />
            <p className="copyright">Rian Mandala Putra &copy;{new Date().getFullYear()}</p>
        </Footer>
    )
}

export default Footer
