import React from 'react'
import styled from '@emotion/styled'

function Projects() {

    const ProjectsContainer = styled.div`
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: 16px;
        padding:0 5%;
        margin: 16px auto;
        div{
            background:#C4C4C4;
            height: 200px;
        }
    `

    return (
        <ProjectsContainer>
            {
                [0,1,2,3].map((_,index)=>(
                    <div key={index}></div>
                ))
            }
            <div></div>
        </ProjectsContainer>
    )
}

export default Projects
