import React from 'react'
import styled from '@emotion/styled'
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import breakPoint from '../utils/breakPoint';

function Contact() {

    const sendMessage = (event)=>{
        event.preventDefault()
    }

    const ContactContainer = styled.div`
        display: grid;
        column-gap: 6%;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        width: 90%;
        margin: 50px auto 80px auto;
        form{
            display: grid;
            row-gap: 20px;
            .name, .email, .message{
                padding: 12px;
                background: #1E242C;
                color:#828282;
                border: none;
                outline:none;
            }
            .btn-submit{
                border-color: white;
                outline: none;
                color: white;
            }
        }
        .contact__info{
            color:white;
            table{
                border-spacing: 12px 16px;
                td{
                    vertical-align: baseline;
                    
                }
            }
            .btn-contact{
                color: white;
                background: #262F38;
                border-radius: 4px;
                margin: 16px 6px;
                &:first-of-type{
                    margin-left:12px;
                }
            }
        }
        ${breakPoint('sm')}{
            margin: 50px auto;
            .contact__info{
                margin-top: 40px;
            }
        }
    `

    return (
        <ContactContainer>
            <form onSubmit={sendMessage} noValidate autoComplete="off">
                <input placeholder="name" type="text" className="name" label="input_name" variant="outlined" />
                <input placeholder="email" type="text" className="email" label="input_email" variant="outlined" />
                <textarea placeholder="message" className="message" label="input_message" multiline="true" rows="4" variant="outlined" />
                <Button type="submit" className="btn-submit" variant="outlined">Submit</Button>
            </form>
            <div className="contact__info">
                <table>
                    <tbody>
                        <tr>
                            <td>Address</td>
                            <td>:</td>
                            <td>Desa namo tualang, Kec.biru-biru, Kab.Deli serdang, Indonesia</td>
                        </tr>
                        <tr>
                            <td>Tel</td>
                            <td>:</td>
                            <td>+6283198924523</td>
                        </tr>
                        <tr>
                            <td>Email</td>
                            <td>:</td>
                            <td>rianmandala959@gmail.com</td>
                        </tr>
                    </tbody>
                </table>
                <IconButton className="btn-contact" aria-label="facebook">
                    <FacebookIcon />
                </IconButton>
                <IconButton className="btn-contact" aria-label="instagram">
                    <InstagramIcon />
                </IconButton>
                <IconButton className="btn-contact" aria-label="linkedin">
                    <LinkedInIcon />
                </IconButton>
                <IconButton className="btn-contact" aria-label="whatsapp">
                    <WhatsAppIcon />
                </IconButton>
            </div>

        </ContactContainer>
    )
}

export default Contact
